//lets import the front to use =>
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap" );
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Spartan', sans-serif;
   /*  background: chartreuse; */
}

:root{
    --PrimaryColor:hsl(225, 58%, 48%);
    --SecondaryColor:hsl(180, 17%, 95%);
    --colorOne:hsl(15, 74%, 70%);
    --colorTwo:hsl(28, 100%, 82%);
    --HoverColor:hsl(225, 56%, 59%);
    --whiteColor:hsl(0, 0%, 100%);
    --blackColor:hsl(0, 0%, 12%);
    --textColor:hsl(240, 1%, 48%);
    --borderColor:hsl(0, 0%, 83%);
    --whiteColorDeam:hsl(0, 0, 93%);
    --greyBg:hsl(0, 0%, 96%);
    --greyText:rgb(145, 145, 145);
    --inputColor:hsl(330, 12%, 97%);
   /*  --boxShadow: box-shadow: rgba(0,0,0,0.1) 0px 10px 15px -3px, rgba(0,0,0,0.05), 0px 4px 6px -2px; */
    --transition: .4s ease-in-out;
    

}

//Default styling ==========>

body{
    background: var(--whiteColor);
}

a{
    text-decoration: none;
}

li{
    list-style: none;
    cursor:pointer;


}

.section{
    padding: 4rem 0 2rem;
}

.container{
    padding-left: 15rem;
    padding-right: 1.5rem;

}

.icon{
    font-size: 1.3rem;
    cursor: pointer;
}



.flex{
    display: flex;
    align-items: center;
}
.grid{
    display: grid;
    align-items: center;
}

.none{
    display: none;
}

.btn{
    padding: 0.7rem 1.5rem;
    background: var(--PrimaryColor);
    border: none;
    outline: none;
    border-radius: 3rem;
    cursor: pointer;
    color: var(--whiteColor);

    a{
        color:var(--whiteColor);
        font-weight: 500;
    }

    &:hover{
        background: var(--HoverColor);
    }


}

img, video{
    width: 100%;
    height: auto;
}

input{
    font-size: 100%;
    color: var(--greyText);
}

p{
    font-size: 13px;
    color: var(--greyText);
    line-height: 22px;
}

h4{
    padding: 1rem 0;
    font-weight: 700;
    color: var(--blackColor);
}

h2{
    font-size: 25px;
    padding: 1rem 0;
    color: var(--blackColor);
}

//navbar section styling

.navBar{
    position: fixed;
    flex-direction: column;
    width: 100%;
    z-index: 1000;


    .navBarOne, .navBarTwo{
        justify-content: space-between;
        padding: 1rem;
        width: 100%;
    }

    .navBarOne{
        background: var(--SecondaryColor);

        .icon:hover{
            color: var(--PrimaryColor);
        }

        .atb{
            gap:1rem;

            span{
                color: var(--blackColor);
                font-size: 13px;
                cursor: pointer;

                &:hover{
                    color: var(--PrimaryColor);
                    font-size: 700;
                }
            }
        }
    }
    .navBarTwo{
        display: flex;
        align-items: center;
        justify-content: space-between;
        top: 3rem;
        padding: 1.5rem 1rem;
        position: fixed;
        transition:.3s ease-in-out;

        .logoDiv{
            .logo{
               width: 20%; 
            }
        }
        @media screen and (max-width:1400px){
            .navBarMenu{
                position:absolute;
                background:var(--SecondaryColor);
                padding: 1.5rem;
                border-radius: 10px;
                top: 5rem;
                right: -50%;
                width: 80%;
                transform: translateX(50%);
                z-index: 2000;
                box-shadow: rgba(0,0,0,0.1) 0px 10px 15px -3px, rgba(0,0,0,0.05), 0px 4px 6px -2px;
                border: 3px solid var(--whiteColor);
                transition: .4s ease-in-out;

                .menu{
                    flex-direction: column;
                    gap: .5rem;
                    text-align: center;

                    .listItem{
                        padding: 0.5rem 0;
                        cursor: pointer;
                        color: var(--blackColor);

                        &:hover{
                            color: var(--PrimaryColor);
                            font-weight: 700;
                        }
                    }
                   
                }
                 .btnOne{
                     display: block;
                     justify-content: center;
                     margin: auto;
                     margin-top: 1rem;
                 }

            }

            .btnTwo{
                display: none;
            }
        }
        .toggleIcon{
           
            .icon{
                font-size: 10px;
                color: var(--blackColor);
                width: 30px;
                height: 30px;
            }
        }

        .showNavBar{
            right: 50%;
            transition: .5s ease-in-out;
        }
    }
    .navbar_with_Bg{
        background: var(--SecondaryColor);
        padding: 0.5rem 1rem;
        box-shadow: rgba(0,0,0,0.1) 0px 10px 15px -3px, rgba(0,0,0,0.05), 0px 4px 6px -2px;
        width: 100%;
        padding-left: 0;

    }
}

//Home section styling=======>

.home{
    padding-top: 10rem;
    text-align: center;
    gap:2rem;
    flex-direction: column;

    .mainText{
        color: var(--blackColor);
        font-weight: 700;
        font-style: 15px;
        line-height: 2.5rem;
    }
    .homeImages{
        width: 100%;
        margin: 1rem auto 0; //This will change on the wider screen
        justify-content: center;
        align-items: center;
        position: relative;
        border-radius: 5rem;

        .videoDiv{
            width: 100%;
            .video{
               border-radius: 10rem;
            }
        }
        .plane{
            position: absolute;
            width: 100%;
            top:-10%;

        }
    }
}

//Search section styling=======>

.search{
    .sectionContainer{
        width: 100%;
        padding: 1.5rem;
        bottom: 2px solid var(--greyBg);
        border-radius: 16px;
        row-gap: 2rem;

        .btns{
            width: 200px;
            background: var(--SecondaryColor);
            padding: .5rem;
            border-radius: 1rem;
            flex-direction: column;
            margin: auto;

            .singlebtn{
                padding: 0.8rem 1.5rem;
                font-size: 13px;
                border-radius: 3rem;
                width: 80%;
                text-align: center;

                &:hover{
                    background: var(--PrimaryColor);
                    color: var(--whiteColor);
                    font-weight: 500;
                    transition: .4s ease;
                }

            }
            .active{
                background: var(--PrimaryColor);
                color: var(--whiteColor);
                font-weight: 500;
            }
           
        }
       

    }
    .searchInputs{
        width: 100%;
        justify-content: center;
        flex-direction:column;
       gap:1.5rem;

       .singleInput{
           .iconDiv{
               padding:16px 10px;
               border-radius: 3rem;
               background: var(--SecondaryColor);
               margin-right: 1rem;

               .icon{
                   color: var(--blackColor);
                   opacity: .7;

               }
           }
           .texts{
               h4{
                   padding-top: 0;
                   padding-bottom: .7rem;
                   color: var(--blackColor);
                   font-weight: 700;
               }
               input{
                   width: 95px;
                   border:none;
                   outline: none;
                   background: transparent;

                   &::placeholder{
                       font-size: 12px;
                       opacity: .5;
                   }
               }
           }
       }
    }
}

//Support section styling=======>
.support{
    .sectionContainer{
       .titleDiv{
           text-align: center;
           small{
               letter-spacing: 0.8rem;
               text-transform: uppercase;
               font-size: 13px;
               color: var(--blackColor);
               line-height: 1.5rem;
           }
         
       }
       .infoDiv{
        margin-top: 2rem;
        gap:1.5rem;

        .textDiv{
            gap:1.5rem;

            .singleInfo{
                .number{
                    background: var(--PrimaryColor);
                    padding: 5px 12px;
                    width: 40px;
                    border-radius:3rem;
                    text-align: center;
                    font-size: 13px;
                    font-weight: 700;
                    color: var(--whiteColor);
                }
                .colorOne{
                    background:var(--colorOne);
                }
                .colorTwo{
                    background:var(--colorTwo);
                }
            }
        }
        
   }
   .imgDiv{
    margin: auto;
    img{
       width: 100%;
       max-width: 420px; 
       height: 400px;
    } 
 }
    }
}


//info section styling=======>
.info{
    background: var(--SecondaryColor);
    .infoContainer{
      .titleDiv{
        flex-direction: column;
        text-align: center;
        gap: 1.5rem;
        justify-content: space-between;
      }
      .cardsDiv{
          gap:1.5rem;
          margin-top: 3rem;

          .singleCard{
            background: var(--whiteColor);
            padding: 3rem 2rem;
            border-radius: 10rem;
            gap: 1rem;
            text-align: center;
            margin: auto;
            height: 320px;
            width: 90%;
            max-width: 250px;

            .iconDiv{
                justify-content: center;
                margin: auto;
                background: var(--PrimaryColor);
                width: max-content;
                height: 55px;
                padding: 1rem;
                border-radius: 3rem;  
            }
            .cardTitle{
                color: var(--blackColor);
                font-weight: 700;
            }

            .colorOne{
                background: var(--colorOne);
            }
            .colorTwo{
                background: var(--colorTwo);
            }
          }
      }
    }
}

//lounge section styling=======>
.lounge{
    .sectionContainer{
       gap: 1.5rem;
       
       .imgDiv1{
           margin:auto;
           img{
               width: 40%;
               max-width: 430px;
               border-radius: 600px;
           }
       }
       .imgDiv{
        margin:auto;
        img{
            width: 100%;
            max-width: 430px;
            border-radius: 300px;
            
        }
    }
       .textDiv{
           .grids{
               margin-top: 1rem;
               gap: 1.5rem;

               .singleGrid{
                  .gridTitle{
                      display:block;
                      padding-bottom: .5rem;
                      font-weight: 500;
                      color: var(--blackColor);
                  }
               }
           }
       }
    }
}

//Travelers section styling=======>

.travelers{
    .sectionContainer{
        text-align: center;

        .travelersContainer{
            margin-top: 1rem;
            gap:1.5rem;

            .singleTraveler{
                position:relative;
                height: 320px;
                width: 85%;
                max-width: 250px;
                overflow: hidden;
                border-radius: 10rem;
                gap: 1rem;
                margin: auto;

                .destinationImage, .travelerImage,.travelerName{
                    position: absolute;
                }
                .destinationImage{
                    height: 100%;
                    width: 100%;
                    top: 0;
                    left: 0;
                    right: 0;bottom: 0;
                    object-fit: cover;
                }
                .travelerImage{
                    height: 50px;
                    width: 50px;
                    object-fit: cover;
                    border-radius: 50%;
                    bottom: 30px;
                    left: 40%;
                    transform:translateX(-50%);
                    border: 5px  solid rgba(225,225,225,0.637);
                    transition: var(--transition);
                    animation: animated 2.3s infinite;
                }
                @keyframes animated{
                    0%{
                        transform:translateY(0);
                    }
                    50%{
                        transform:translateY(8px);
                    }
                    100%{
                        transform:translateY(0px);
                    }

                    }
               
                    .travelerName{
                        bottom: -100%;
                        width: 100%;
                        height:max-content;
                        background: var(--whiteColor);
                        padding: 1.5rem 1rem;
                        transition: var(--transition);
                        animation: animatedBorders 2.3s infinite;

                        span{
                            display: block;
                            color: black;
                            font-weight: 700;
                        }

                        p{
                            color: var(--PrimaryColor);
                            font-style: italic;
                        }
                }
                @keyframes animatedBorders{
                    0%{
                        border-radius: 0rem;
                    }
                    50%{
                        transform:2rem;
                    }
                    100%{
                        transform:0rem;
                    }

                    }
               &:hover{
                   .travelerName{
                       bottom: 0;
                       transition: 1s ease-in-out;
                   }

                   .travelerImage{
                       bottom: 110px;
                       transition: 1s ease-in-out;
                   }
               }
            }

        }
    }
}

//Subscribe section styling=======>
.subscribe{
     background:var(--SecondaryColor);

     .sectionContainer{
         text-align: center;
         .inputDiv{
             margin-top: 1rem;
             flex-direction: column;
