        padding-top: 12rem;
    }

    //search section Styling========================>

    .search{
        .sectionContainer{
            .searchInputs{
                display: grid;
                grid-template-columns: repeat(2,1fr);
            }
        }
    }
     //Travelers section Styling========================>
    
     .travelers{
         .sectionContainer{
             .travelersContainer{
                /*  grid-template-columns: repeat(2,1fr); */
             }
         }
     }
      //Subscribe section Styling========================>

      .subscribe{
          .sectionContainer{
              max-width: 80%;
              margin: auto; 

              .inputDiv{
                  flex-direction: row;
                  padding: 0 .5rem;
                  border-radius: 3rem;
              }
          }
      }

       //Footer section Styling========================>

       .footer{
           .sectionContainer{
               align-items: flex-start;
               grid-template-columns: repeat(2,1fr);

